const localServer = !true;
const production = process.env.NODE_ENV === 'production';

const appSettings = {
    production,
    reduxLogger: !true,
    api: {
        baseUrl: localServer
            ? 'http://localhost:4000/api/'
            : 'https://l6217t73j6.execute-api.eu-west-1.amazonaws.com/dev/api/',
        endpoints: {
            users: localServer
                ? 'users'
                : 'accounts',
            employees: 'employees',
        },
    },
    registration: true,
};

export default appSettings;



