import bg from './loginBg.jpg';

const authWrapperStyles = (theme) => {
    const {
        unit,
    } = theme.spacing;
    const margin = 3 * unit;
    const loginWidth = 52 * theme.spacing.unit;
    return {
        root: {
            display: 'flex',
            justifyContent: 'flex-end',
            height: '100vh',
            background: theme.palette.primary.main,
        },
        infoWrapper: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            position: 'absolute',
            top: 0,
            right: loginWidth,
            bottom: 0,
            left: 0,
            background: `url(${bg}) no-repeat center center fixed`,
            backgroundSize: 'cover',
        },
        header: {
            color: theme.palette.primary.contrastText,
            fontWeight: 300,
            maxWidth: 54 * theme.spacing.unit,
            textAlign: 'center',
            textShadow: '1px 2px 4px rgba(0, 0, 0, .7)',
            fontFamily: '\'Caveat\', cursive',
            fontSize: '52px',
        },
        contentWrapper: {
            width: loginWidth,
            maxWidth: loginWidth,
            minWidth: loginWidth,
            background: theme.palette.background.paper,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'flex-start',
            justifyContent: 'space-between',
            padding: `${margin}px ${4 * unit}px`,
            boxSizing: 'border-box',
        },
        linear: {
            position: 'absolute',
            left: 0,
            right: 0,
        },
        logo: {
            color: theme.palette.secondary.main,
            fontFamily: '\'Caveat\', cursive',
            fontWeight: 'bold',
            fontSize: '40px',
            textShadow: '1px 2px 4px rgba(0, 0, 0, .3)',
        },
    };
};

export default authWrapperStyles;



