import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withStyles } from 'material-ui/styles';
import Drawer from 'material-ui/Drawer';
import IconButton from 'material-ui/IconButton';
import Typography from 'material-ui/Typography';
import HomeIcon from 'material-ui-icons/Home';
import AnalyticsIcon from 'material-ui-icons/InsertChart';
import UsersIcon from 'material-ui-icons/Person';
import TeamMembersIcon from 'material-ui-icons/SupervisorAccount';
import ContentIcon from 'material-ui-icons/Edit';
import CompaniesIcon from 'material-ui-icons/Business';
import HomeWrapperLogutAvatar from './HomeWrapperLogutAvatar';

const styles = (theme) => {
    const {
        unit,
    } = theme.spacing;
    const drawerWidth = 8 * unit;
    return {
        root: {
            position: 'relative',
            width: drawerWidth,
            backgroundColor: theme.palette.primary.main,
            color: theme.palette.primary.contrastText,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
        },
        toolbar: {
            ...theme.mixins.toolbar,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        logo: {
            color: theme.palette.secondary.main,
            fontFamily: '\'Caveat\', cursive',
            fontWeight: 'bold',
            fontSize: 30,
            marginLeft: -unit,
            marginTop: -4,
        },
        navIcons: {
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
        },
    };
};

function HomeWrapperDrawer(props) {
    const {
        classes,
    } = props;
    return (
        <Drawer
            variant="permanent"
            classes={{
                paper: classes.root,
            }}
        >
            <div className={classes.toolbar}>
                <Typography
                    variant="title"
                    className={classes.logo}
                >
                    87%
                </Typography>
            </div>
            <div className={classes.navIcons}>
                <IconButton
                    color="inherit"
                    aria-label="Home"
                    component={Link}
                    to="/home/main"
                >
                    <HomeIcon />
                </IconButton>
                <IconButton
                    color="inherit"
                    aria-label="Analytics"
                    component={Link}
                    to="/home/analytics"
                >
                    <AnalyticsIcon />
                </IconButton>
                <IconButton
                    color="inherit"
                    aria-label="Employees"
                    component={Link}
                    to="/home/employees"
                >
                    <UsersIcon />
                </IconButton>
                <IconButton
                    color="inherit"
                    aria-label="Content"
                    component={Link}
                    to="/home/content"
                >
                    <ContentIcon />
                </IconButton>
                <IconButton
                    color="inherit"
                    aria-label="Team members"
                    component={Link}
                    to="/home/team-members"
                >
                    <TeamMembersIcon />
                </IconButton>
                <IconButton
                    color="inherit"
                    aria-label="Companies"
                    component={Link}
                    to="/home/companies"
                >
                    <CompaniesIcon />
                </IconButton>
            </div>
            <HomeWrapperLogutAvatar />
        </Drawer>
    );
}

HomeWrapperDrawer.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(HomeWrapperDrawer);



