import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import { LinearProgress } from 'material-ui/Progress';
import authWrapperStyles from './authWrapperStyles';

function AuthWrapper(props) {
    const {
        isPending,
        children,
        classes,
    } = props;
    const progress = isPending
        ? (
            <div className={classes.linear}>
                <LinearProgress color="secondary" />
            </div>
        )
        : '';
    return (
        <div className={classes.root}>
            <div className={classes.infoWrapper}>
                <Typography
                    variant="display1"
                    className={classes.header}
                >
                    Reflect on your wellbeing,
                    set activities to help you
                    improve your wellbeing
                    and track your progress.
                </Typography>
            </div>
            <div className={classes.contentWrapper}>
                <Typography
                    variant="title"
                    className={classes.logo}
                >
                    87%
                </Typography>
                {children}
                <Typography variant="caption">
                    Copyright &copy; 2018 87% Ltd. All right reserved.
                </Typography>
            </div>
            {progress}
        </div>
    );
}

AuthWrapper.propTypes = {
    isPending: PropTypes.bool.isRequired,
    classes: PropTypes.object.isRequired,
    children: PropTypes.oneOfType([
        PropTypes.array,
        PropTypes.object,
    ]).isRequired,
};

export default withStyles(authWrapperStyles)(AuthWrapper);



