/* eslint jsx-a11y/anchor-is-valid: 0 */
import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';

const styles = (theme) => {
    const {
        unit,
    } = theme.spacing;
    const margin = 3 * unit;
    return {
        form: {
            width: '100%',
        },
        submitWrapper: {
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
        },
        submitLink: {
            flex: 1,
        },
        submitButton: {
            margin: `${margin}px 0`,
        },
        error: {
            color: theme.palette.error.main,
        },
    };
};

function AuthFormWrapper(props) {
    const {
        formLabel,
        linkTo,
        linkLabel,
        buttonDisabeld,
        buttonLabel,
        errorLabel,
        infoLabel,
        children,
        classes,
    } = props;
    const error = errorLabel
        ? (
            <Typography
                variant="body2"
                className={classes.error}
            >
                {errorLabel}
            </Typography>
        )
        : '';
    const info = infoLabel
        ? (
            <Typography
                variant="body2"
            >
                {infoLabel}
            </Typography>
        )
        : '';
    return (
        <form
            noValidate
            autoComplete="off"
            onSubmit={event => props.formOnSubmit(event)}
            className={classes.form}
        >
            <Typography
                variant="headline"
                gutterBottom
            >
                {formLabel}
            </Typography>
            {error}
            {info}
            {children}
            <div className={classes.submitWrapper}>
                <Typography
                    variant="body1"
                    className={classes.submitLink}
                >
                    <Link to={linkTo}>
                        {linkLabel}
                    </Link>
                </Typography>
                <Button
                    variant="raised"
                    color="primary"
                    type="submit"
                    disabled={buttonDisabeld}
                    className={classes.submitButton}
                >
                    {buttonLabel}
                </Button>
            </div>
        </form>
    );
}

AuthFormWrapper.propTypes = {
    formOnSubmit: PropTypes.func.isRequired,
    formLabel: PropTypes.string.isRequired,
    linkTo: PropTypes.string.isRequired,
    linkLabel: PropTypes.string.isRequired,
    buttonDisabeld: PropTypes.bool.isRequired,
    buttonLabel: PropTypes.string.isRequired,
    errorLabel: PropTypes.string.isRequired,
    infoLabel: PropTypes.string.isRequired,
    children: PropTypes.oneOfType([
        PropTypes.array,
        PropTypes.object,
    ]).isRequired,
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AuthFormWrapper);



