import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from 'material-ui/styles';
import Tooltip from 'material-ui/Tooltip';
import Button from 'material-ui/Button';
import ExitToAppIcon from 'material-ui-icons/ExitToApp';
import { logout } from '../../../Auth/authActions';

const styles = (theme) => {
    const {
        unit,
    } = theme.spacing;
    const drawerWidth = 8 * unit;
    return {
        root: {
            marginBottom: (drawerWidth - 40) / 2,
        },
    };
};

const initialState = {
    showLogout: false,
};

class HomeWrapperLogutAvatar extends Component {
    constructor(props) {
        super(props);
        this.state = initialState;
    }
    handleSwitchView(status) {
        this.setState({
            showLogout: status,
        });
    }
    handleLogout() {
        if (!this.props.isPending) {
            this.props.logout()
                .then(() => {})
                .catch(() => {});
        }
    }
    render() {
        const {
            user,
            classes,
        } = this.props;
        const {
            showLogout,
        } = this.state;
        const letter = (user.username || user.email).charAt(0).toUpperCase();
        const icon = showLogout
            ? <ExitToAppIcon />
            : letter;
        return (
            <div
                className={classes.root}
            >
                <Tooltip
                    id="tooltip-avatar"
                    title="Logout"
                    placement="top"
                >
                    <Button
                        variant="fab"
                        mini
                        aria-label="Logout"
                        onMouseEnter={() => this.handleSwitchView(true)}
                        onMouseLeave={() => this.handleSwitchView(false)}
                        onClick={() => this.handleLogout()}
                    >
                        {icon}
                    </Button>
                </Tooltip>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    user: state.authReducer.user,
    isPending: state.authReducer.isPending,
});

const mapDispatchToProps = dispatch => ({
    logout: () => dispatch(logout()),
});

HomeWrapperLogutAvatar.propTypes = {
    user: PropTypes.object.isRequired,
    isPending: PropTypes.bool.isRequired,
    logout: PropTypes.func.isRequired,
    classes: PropTypes.object.isRequired,
};

export default compose(
    withStyles(styles),
    connect(
        mapStateToProps,
        mapDispatchToProps,
    ),
)(HomeWrapperLogutAvatar);



