import React from 'react';
import PropTypes from 'prop-types';
import TextField from 'material-ui/TextField';
import isEmail from '../../../common/isEmail';
import AuthFormWrapper from '../components/AuthFormWrapper';

function Login(props) {
    const {
        email,
        password,
        hasError,
        isPending,
    } = props;
    const submitDisabeld = isPending || !password || !isEmail(email);
    const errorLabel = hasError && !isPending
        ? 'Login attempt failed, are the username and password correct?'
        : '';
    return (
        <AuthFormWrapper
            formOnSubmit={event => props.handleLogin(event)}
            formLabel="Log in"
            linkTo="/auth/reset-password"
            linkLabel="Forgot password?"
            buttonDisabeld={submitDisabeld}
            buttonLabel="Log in"
            errorLabel={errorLabel}
            infoLabel=""
        >
            <TextField
                name="email"
                label="Email"
                type="email"
                value={email}
                onChange={event => props.handleInputChange(event)}
                disabled={isPending}
                margin="normal"
                fullWidth
                autoFocus
            />
            <TextField
                name="password"
                label="Password"
                type="password"
                value={password}
                onChange={event => props.handleInputChange(event)}
                disabled={isPending}
                margin="normal"
                fullWidth
            />
        </AuthFormWrapper>
    );
}

Login.propTypes = {
    email: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    hasError: PropTypes.bool.isRequired,
    isPending: PropTypes.bool.isRequired,
    handleLogin: PropTypes.func.isRequired,
    handleInputChange: PropTypes.func.isRequired,
};

export default Login;



