import React from 'react';
import PropTypes from 'prop-types';
import TextField from 'material-ui/TextField';
import isEmail from '../../../common/isEmail';
import AuthFormWrapper from '../components/AuthFormWrapper';

function ResetPassword(props) {
    const {
        email,
        sent,
        isPending,
        hasError,
    } = props;
    const submitDisabeld = isPending || !isEmail(email);
    const errorLabel = hasError
        ? 'Email not found'
        : '';
    const infoLabel = sent
        ? 'We\'ve sent you reset password instructions.'
        : '';
    const buttonLabel = !sent
        ? 'Send Email'
        : 'Resend Email';
    const view = !sent
        ? (
            <TextField
                name="email"
                label="Email"
                type="email"
                value={email}
                onChange={event => props.handleInputChange(event)}
                disabled={isPending}
                margin="normal"
                fullWidth
                autoFocus
            />
        )
        : (
            <div />
        );
    return (
        <AuthFormWrapper
            formOnSubmit={event => props.handleResetPassword(event)}
            formLabel="Reset Password"
            linkTo="login"
            linkLabel="Log in"
            buttonDisabeld={submitDisabeld}
            buttonLabel={buttonLabel}
            errorLabel={errorLabel}
            infoLabel={infoLabel}
        >
            {view}
        </AuthFormWrapper>
    );
}

ResetPassword.propTypes = {
    email: PropTypes.string.isRequired,
    sent: PropTypes.bool.isRequired,
    isPending: PropTypes.bool.isRequired,
    hasError: PropTypes.bool.isRequired,
    handleResetPassword: PropTypes.func.isRequired,
    handleInputChange: PropTypes.func.isRequired,
};

export default ResetPassword;



