import { createMuiTheme } from 'material-ui/styles';
import grey from 'material-ui/colors/grey';
import deepOrange from 'material-ui/colors/deepOrange';

const appTheme = createMuiTheme({
    palette: {
        primary: {
            main: grey[900],
        },
        secondary: {
            main: deepOrange[500],
        },
        error: {
            main: deepOrange[500],
        },
    },
});

export default appTheme;



