import {
    GET_CURRENT_USER,
    LOGIN,
    RESET_PASSWORD,
    LOGOUT,
} from '../../constants/ActionTypes';
import authInitialState from './authInitialState';

function authReducer(state = authInitialState, action) {
    switch (action.type) {
    case `${GET_CURRENT_USER}_PENDING`:
        return {
            ...state,
            appIsLoading: true,
        };
    case `${GET_CURRENT_USER}_FULFILLED`:
        return {
            ...state,
            user: action.payload,
            appIsLoading: false,
        };
    case `${GET_CURRENT_USER}_REJECTED`:
        return {
            ...state,
            appIsLoading: false,
        };
    case `${LOGIN}_PENDING`:
        return {
            ...state,
            isPending: true,
        };
    case `${LOGIN}_FULFILLED`:
        return {
            ...state,
            isPending: false,
            user: action.payload,
        };
    case `${LOGIN}_REJECTED`:
        return {
            ...state,
            isPending: false,
        };
    case `${RESET_PASSWORD}_PENDING`:
        return {
            ...state,
            isPending: true,
        };
    case `${RESET_PASSWORD}_FULFILLED`:
        return {
            ...state,
            isPending: false,
        };
    case `${RESET_PASSWORD}_REJECTED`:
        return {
            ...state,
            isPending: false,
        };
    case `${LOGOUT}_PENDING`:
        return {
            ...state,
            isPending: true,
        };
    case `${LOGOUT}_FULFILLED`:
        return {
            ...state,
            isPending: false,
            user: {},
        };
    case `${LOGOUT}_REJECTED`:
        return {
            ...state,
            isPending: false,
        };
    default:
        return state;
    }
}

export default authReducer;



